import React,{useEffect,useState} from 'react'
import {domain} from '../../../connection/Config'
import {Grid,Avatar,Container,Button} from '@material-ui/core';
import '../../../css/meals_manage.css'
import Search_meal from './Search_meal';
  

export default function Meals_manage({setLoader,token}) {
    const[itemId,setItemId]=useState(0)
    const[available,setAvailable]=useState('')
    const[items,setItems]=useState([])
    const[allItems,setAllItems]=useState([])
    useEffect(()=>{
        
            setLoader(true)
            const get_dishes=async()=>{
                try{
                const res= await fetch(`${domain}/dishes/${token}`)
                const data = await res.json()
                setItems(data.meals)
                setAllItems(data.meals)
                console.log(data.meals)
            }
                catch(err){
                    setLoader(false)
                    console.log(err)
                }
            }
            get_dishes()
        setLoader(false)
    },[])
    
    const notAvialable= async(id,index)=>{
        setLoader(true)
        try{
            const res = await fetch(`${domain}/dishes`,
            {
                method: 'PUT',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(
                    {
                   id:id.id
                    }
                    )
            }
            )
            const data= await res.json()
          if(data.meals){
            let newArr=[...items]
            newArr[index].available = false
            setItems(newArr)
          }
          else{
            alert('משהו השתבש בבקשה, נסו שוב או פנו לרום')
        }
        }
        catch(err){
            console.log(err)
            setLoader(false)
        }
        setLoader(false)
    }
    const avialable= async(id,index)=>{
        setLoader(true)
        try{
            const res2 = await fetch(`${domain}/dishes`,
            {
                method: 'PUT',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(
                    {
                        id:id.id
                    }
                    )
            }
            ) 
            const data2= await res2.json()
            if(data2.meals){
                let newArr=[...items]
                newArr[index].available = true
                setItems(newArr)
            }
            else{
                alert('משהו השתבש בבקשה, נסו שוב או פנו לרום')
            }
        }
        catch(err){
            console.log(err)
            setLoader(false)
        }
       
        setLoader(false)
    }
    
    const changeAll_available= async()=>{
        setLoader(true)
        try{
            
            const res = await fetch(`${domain}/dishes`,
            {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(
                    {
                        id:token,
                        available:available
                    }
                    )
            }
            ) 
            const data= await res.json() 
            if(data.status =='changed'){
               setItems(data.newArr)
            }
            else{
                alert('משהו השתבש בבקשה, נסו שוב או פנו לרום')
            }
        }
        catch(err){
            console.log(err)
            setLoader(false)
        }
        setLoader(false)
    }
    return (
        <Container style={{minHeight:'85vh'}}>
            <div className='list-warper'>
            <Grid className='header-warper' style={{ backgroundColor: "rgb(221, 235, 243)"}}>
                <Grid>
                <Search_meal 
                   items={items} 
                   setItems={setItems}
                    allItems={allItems}/> 
                </Grid>
                <Grid style={{marginBottom:'10px'}}>
                 <button onClick={changeAll_available} className='bt-available'>עדכן את כל המנות </button>
                 <select onChange={(e)=>{setAvailable(e.target.value)}} className='bt-available'>
                 <option value="available">כל המנות זמינות</option>
                 <option value="not_available">כל המנות לא זמינות</option>
                 </select>
                </Grid>
                   
                 </Grid>
            {items?
            <Grid className='warper-meals'>
                <Grid>
                    <Grid>
                        <h2>שינוי זמינות מנה</h2>
                    </Grid>
                </Grid>
            {items.map((el,index)=>{
                return(
            <Grid className='row' >
             <Grid className='bt-warper' xl={3} md={3} lg={3}>
                 {el.available ==true?
                  <Button onClick={()=>{notAvialable(el,index)}} className='bt_avialable'   variant='outlined'>זמין</Button>
                :<Button onClick={()=>{avialable(el,index)}} className='bt_avialable'  variant='outlined'>לא זמין</Button> }
             </Grid>
           
            <Grid xl={3} md={3} lg={3} className="item_cover">
            <h5>{el.name}</h5>
             </Grid>
             <Grid xl={3} md={3} lg={3} className="item_cover">
                <Avatar className="avatar"   src={el.image}/>
             </Grid>
             </Grid>
             
             ) 
            })}
          </Grid>
        :<h3>אין מנות לבשלן....</h3>}
         </div>
        </Container>
    )
}
