import React,{useEffect,useState} from 'react'
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import { TextField,Grid } from '@material-ui/core';
import {domain} from '../../../connection/Config'
import Rejected from './Rejected';
import '../../../css/orders.css'
import NewOrder from './NewOrder';

export default function Orders_popup({setPopupTriger,setId_order,id_order,newOrder,token}) {
    const [open, setOpen] = React.useState(false);
    const [timer,setTimer]=useState(0)
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  
    useEffect(() => {
        setOpen(true);
    }, [])
     
    
      const delivery= async(e)=>{
        e.preventDefault()
        newOrder[0].time= timer
        setOpen(false);
        setPopupTriger(false)
        const res2 = await fetch(`${domain}/orders/delivery`,
        {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(
                {
                    id:token,
                    items:newOrder[0],
                    time:timer,
                }
                ) 
        }
        )
 
      }

    const handleClose = async() => {
      setOpen(false);
      setPopupTriger(false)
      const res2 = await fetch(`${domain}/orders`,
      {
          method: 'PUT',
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          },
          body: JSON.stringify(
              {
                  id:id_order,
              }
              ) 
      }
      )
      setId_order(0)
    };
    
    return (
      <div>
      
        <Dialog
          fullScreen={fullScreen}
          open={open}
          onClose={handleClose}
          aria-labelledby="responsive-dialog-title"
        >
            <form onSubmit={delivery}>
          <DialogTitle id="responsive-dialog-title">התקבלה הזמנה חדשה</DialogTitle>
          <DialogContent>
          <DialogContentText>
             {newOrder[0].items.map(item=>{
               return(
                <h3>{item.amount} . {item.name}   </h3>
               )
               
             })
             }
           </DialogContentText>
        
           <DialogContentText>
           ? כמה זמן יקח למשלוח להיות מוכן
           </DialogContentText>
          
            <DialogContentText className='bt-warper'>
             <TextField required onChange={(e)=>{setTimer(e.target.value)}}   label=":זמן הכנה" variant='outlined' type='number'/>
            </DialogContentText>
          </DialogContent>
             <Grid className='bt-warper'>
             <Rejected/>
            <Button   type='submit' className='agree' variant='outlined' >
             אישור
            </Button>
             </Grid>
            
            </form>
        </Dialog>
      </div>
    );
}
